import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.scss']
})
export class HeaderComponent implements OnInit {



  constructor () {}

  ngOnInit() {

      };

  ocultarTextoAlert():void {
    const altura = window.scrollY;
    const subTitle:HTMLElement | null = document.querySelector(".sub-title");

    altura > 150?  subTitle!.style.display = "none" : subTitle!.style.display = "block" 
  }

}
